.mainField {
    width: 50%;
    float: left;
}
.field {
    background: white;
    border-radius: 10px;
    border: 5px solid gray;
    width: 70%;
    margin: 0 auto;
    padding: 20px 0;
}
.mainField h2 {
    color: #ec1414;
    font-size: 32px;
    margin-bottom: 10px;
}
.totalCases {
    color: #0a0fff;
    font-size: 26px;
    margin-bottom: 10px;
}
.recovered {
    color: #ff762c;
    font-size: 26px;
    margin-bottom: 10px;
}
.deaths {
    font-size: 26px;
}