.mainField {
    width: 50%;
    float: right;
}
.field {
    background: white;
    border-radius: 10px;
    border: 5px solid gray;
    width: 70%;
    margin: 0 auto;
    padding: 20px 0;
}
.mainField h2 {
    color: #ec1414;
    font-size: 32px;
    margin-bottom: 20px;
}
.newCases {
    color: #0a0fff;
    font-size: 26px;
    margin-bottom: 20px;
}
.newDeaths {
    font-size: 26px;
    margin-bottom: 20px;
}